<template>
  <div class="itemBox">
    <p class="title">页面设置</p>
    <el-form ref="form" :rules="rules" :model="itemData" size="mini">
      <el-form-item label="页面名称" prop="title">
        <el-input v-model.trim="itemData.title" maxlength="10" placeholder="请输入页面名称"></el-input>
      </el-form-item>
      <el-form-item label="页面描述">
        <el-input v-model="itemData.describe" maxlength="28" placeholder="请输入页面描述"></el-input>
      </el-form-item>
      <el-form-item label="背景颜色">
        <el-radio-group class="bgColor" v-model="itemData.bgSelect" @change="bgChange">
          <el-radio :label="0" class="custom">默认背景色</el-radio>
          <el-radio :label="1" class="custom">
            <span class="custom">
              <span>自定义背景色</span>
              <el-color-picker
                v-show="itemData.bgSelect==1"
                v-model="itemData.backgroundColor"
                :predefine="predefineColors"
              ></el-color-picker>
            </span>
          </el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      rules: {
        title: [{ required: true, message: "请输入页面名称", trigger: "blur" }]
      },
      predefineColors: [
        "#ff4500",
        "#ff8c00",
        "#ffd700",
        "#90ee90",
        "#00ced1",
        "#1e90ff",
        "#c71585"
      ]
    };
  },
  methods: {
    //背景色切换
    bgChange(){
      if (this.itemData.bgSelect == 0) {
        this.itemData.backgroundColor = "#fff";
      }
    }
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 14px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .bgColor {
    display: flex;
    flex-direction: column;
    .el-radio {
      margin: 0 0 0 10px;
      height: 32px;
      width: 104px;
    }
  }
  .navigation {
    .el-radio {
      margin-left: 10px;
      margin-right: 18px;
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
}
</style>
